<template>
    <div class="student-course-wrap">
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item>
                    <b style="font-size: 22px"> </b>
                </el-breadcrumb-item>
            </el-breadcrumb>
        </div>

        <div class="container">
            <div class="query-form">
                <el-row :gutter="20">
                    <el-col :span="3">
                        <el-button @click="create" style="font-size: 18px"
                            >
                            </el-button
                        >
                    </el-col>
                    <el-col :span="4">
                        <el-checkbox
                            @change="updateAllowSelect"
                            v-model="optionForm.allowSelect"
                            >
                        </el-checkbox>
                        <el-checkbox
                            @change="updateAllowGrade"
                            v-model="optionForm.allowGrade"
                            >
                        </el-checkbox>
                    </el-col>
                    <el-col :offset="4" :span="3">
                        <el-input
                            @keyup.enter.native="query"
                            placeholder=""
                            v-model="queryForm.courseName"
                        />
                    </el-col>
                    <el-col :span="3">
                        <el-input
                            @keyup.enter.native="query"
                            placeholder=""
                            v-model="queryForm.studentName"
                        />
                    </el-col>
                    <el-col :span="3">
                        <el-input
                            @keyup.enter.native="query"
                            placeholder=""
                            v-model="queryForm.className"
                        />
                    </el-col>
                    <el-col :span="3">
                        <el-button
                            @click="query"
                            type="primary"
                            style="font-size: 18px"
                        >
                            
                        </el-button>
                    </el-col>
                </el-row>
            </div>

            <div class="table">
                <el-table :data="tableData" stripe>
                    <el-table-column label="Id" prop="id" width="80px" />
                    <el-table-column label="" prop="courseName" />
                    <el-table-column label="" prop="studentName" />
                    <el-table-column label="" prop="className" />
                    <el-table-column
                        label=""
                        min-width="80px"
                        prop="dailyScore"
                    />
                    <el-table-column
                        label=""
                        prop="examScore"
                        width="80px"
                    />
                    <el-table-column label="" prop="score" width="80px" />
                    <el-table-column
                        align="center"
                        label=""
                        width="250px"
                        fixed="right"
                    >
                        <template slot-scope="scope">
                            <el-button
                                @click="edit(scope.row.id)"
                                type="success"
                                style="font-size: 18px"
                            >
                                
                            </el-button>
                            <el-button
                                @click="deleteItem(scope.row.id)"
                                type="danger"
                                style="font-size: 18px"
                            >
                                
                            </el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>

            <el-row justify="center" type="flex" style="margin: 5px 0 5px 0">
                <el-pagination
                    :current-page.sync="pageIndex"
                    :page-size="pageSize"
                    :total="pageSize * pageCount"
                    @current-change="getPage"
                    background
                    layout="prev, pager, next"
                >
                </el-pagination>
            </el-row>

            <el-dialog :visible.sync="editing" title="" width="30%">
                <el-form :model="entityForm" label-width="70px" ref="form">
                    <el-form-item label="">
                        <el-select
                            :disabled="entityForm.id !== -1"
                            placeholder=""
                            v-model="entityForm.studentId"
                        >
                            <el-option
                                :key="index"
                                :label="item.name"
                                :value="item.id"
                                v-for="(item, index) in students"
                            />
                        </el-select>
                    </el-form-item>
                    <el-form-item label="">
                        <el-select
                            :disabled="entityForm.id !== -1"
                            placeholder=""
                            v-model="entityForm.courseId"
                        >
                            <el-option
                                :key="index"
                                :label="item.name"
                                :value="item.id"
                                v-for="(item, index) in courses"
                            />
                        </el-select>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input
                            type="number"
                            v-model="entityForm.dailyScore"
                        ></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input
                            type="number"
                            v-model="entityForm.examScore"
                        ></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input
                            type="number"
                            v-model="entityForm.score"
                        ></el-input>
                    </el-form-item>
                </el-form>
                <span class="dialog-footer" slot="footer">
                    <el-button
                        @click="save"
                        type="primary"
                        style="font-size: 18px"
                        >
                         </el-button
                    >
                    <el-button @click="editing = false" style="font-size: 18px"
                        >
                         </el-button
                    >
                </span>
            </el-dialog>
        </div>
    </div>
</template>

<script>
import * as api from "../../api/admin/studentCourse";
import * as CourseApi from "../../api/admin/course";
import * as StudentApi from "../../api/admin/student";
import * as OptionApi from "../../api/option";

export default {
    name: "AdminStudentCourse",
    data() {
        return {
            queryForm: {
                className: "",
                courseName: "",
                studentName: "",
            },
            optionForm: {
                allowSelect: true,
                allowGrade: true,
            },
            entityForm: {},
            tableData: [],
            pageSize: api.pageSize,
            pageCount: 1,
            pageIndex: 1,
            editing: false,
            courses: [],
            students: [],
        };
    },
    methods: {
        query() {
            api.getPageCount(
                this.queryForm.className,
                this.queryForm.courseName,
                this.queryForm.studentName
            ).then((res) => {
                this.pageCount = res;
                this.pageIndex = 1;
                this.getPage(1);
            });
        },
        getPage(pageIndex) {
            api.getPage(
                pageIndex,
                this.queryForm.className,
                this.queryForm.courseName,
                this.queryForm.studentName
            ).then((res) => {
                this.tableData = res;
            });
        },
        create() {
            this.entityForm = {
                id: -1,
                studentId: null,
                courseId: null,
                dailyScore: null,
                examScore: null,
                score: null,
            };
            this.editing = true;
        },
        edit(id) {
            api.get(id).then((res) => {
                this.entityForm = res;
                this.editing = true;
            });
        },
        save() {
            if (this.entityForm.id === -1) {
                api.create(this.entityForm).then(() => {
                    this.finishSave();
                });
            } else {
                api.update(this.entityForm).then(() => {
                    this.finishSave();
                });
            }
        },
        finishSave() {
            this.$message.success("");
            this.getPage(this.pageIndex);
            this.editing = false;
        },
        deleteItem(id) {
            api.deleteItem(id).then(() => {
                this.$message.success("");
                this.getPage(this.pageIndex);
            });
        },
        getCoursesAndStudents() {
            CourseApi.listName().then((res) => {
                this.courses = res;
            });
            StudentApi.listName().then((res) => {
                this.students = res;
            });
        },
        getSelectAndGradeStatus() {
            OptionApi.getAllowStudentSelect().then((res) => {
                this.optionForm.allowSelect = res;
            });
            OptionApi.getAllowTeacherGrade().then((res) => {
                this.optionForm.allowGrade = res;
            });
        },
        updateAllowSelect(val) {
            OptionApi.setAllowStudentSelect(val);
        },
        updateAllowGrade(val) {
            OptionApi.setAllowTeacherGrade(val);
        },
    },
    created() {
        this.query();
        this.getCoursesAndStudents();
        this.getSelectAndGradeStatus();
    },
};
</script>

<style scoped></style>
