-->
<template>
    <div class="course-wrap">
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item>
                    <b style="font-size: 22px"> </b>
                </el-breadcrumb-item>
            </el-breadcrumb>
        </div>

        <div class="container">
            <div class="table">
                <el-table :data="tableData" stripe>
                    <el-table-column label="Id" prop="studentCourseId" />
                    <el-table-column label="" prop="courseName" />
                    <el-table-column label="" prop="teacherName" />
                    <el-table-column label="" prop="credit" />
                    <el-table-column label="" prop="dailyScore" />
                    <el-table-column label="" prop="examScore" />
                    <el-table-column label="" prop="score" />
                    <el-table-column
                        align="center"
                        label=""
                        width="200px"
                        fixed="right"
                    >
                        <template slot-scope="scope">
                            <el-button
                                @click="deleteItem(scope.row.studentCourseId)"
                                type="danger"
                                style="font-size: 18px"
                                :disabled="
                                    scope.row.dailyScore != undefined ||
                                    scope.row.examScore != undefined ||
                                    scope.row.score != undefined
                                "
                            >
                                 
                            </el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
        </div>
    </div>
</template>

<script>
import * as api from "../../api/student/course";

export default {
    name: "StudentCourse",
    data() {
        return {
            tableData: [],
        };
    },
    methods: {
        getList() {
            api.list().then((res) => {
                this.tableData = res;
            });
        },
        deleteItem(studentCourseId) {
            api.deleteItem(studentCourseId).then(() => {
                this.$message.success("!");
                this.getList();
            });
        },
    },
    created() {
        this.getList();
    },
};
</script>

<style scoped></style>
