<template>
    <div class="score-wrap">
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item>
                    <b style="font-size: 22px"> </b>
                </el-breadcrumb-item>
            </el-breadcrumb>
        </div>

        <div class="container">
            <div class="table">
                <el-table :data="tableData" stripe>
                    <el-table-column label="Id" prop="studentCourseId" />
                    <el-table-column label="" prop="courseName" />
                    <el-table-column label="" prop="teacherName" />
                    <el-table-column label="" prop="credit" />
                    <el-table-column label="" prop="dailyScore" />
                    <el-table-column label="" prop="examScore" />
                    <el-table-column label="" prop="score" />
                </el-table>
            </div>
        </div>
    </div>
</template>

<script>
import * as api from "../../api/student/course";

export default {
    name: "StudentScore",
    data() {
        return {
            tableData: [],
        };
    },
    methods: {
        getList() {
            api.list().then((res) => {
                this.tableData = this.scoreFilter(res);
            });
        },
        scoreFilter(data) {
            let filtered = [];
            for (let i = 0; i < data.length; i++) {
                let item = data[i];
                if (
                    item.dailyScore != null ||
                    item.examScore != null ||
                    item.score != null
                ) {
                    filtered.push(item);
                }
            }
            return filtered;
        },
    },
    created() {
        this.getList();
    },
};
</script>

