<template>
    <div class="course-wrap">
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item>
                    <b style="font-size: 22px"> </b>
                </el-breadcrumb-item>
            </el-breadcrumb>
        </div>

        <div class="container">
            <div class="query-form">
                <el-row :gutter="20">
                    <el-col :span="2">
                        <el-button @click="create" style="font-size: 18px"
                            >
                            </el-button
                        >
                    </el-col>
                    <el-col :offset="10" :span="3">
                        <el-input
                            @keyup.enter.native="query"
                            placeholder=""
                            v-model="queryForm.courseName"
                        />
                    </el-col>
                    <el-col :span="3">
                        <el-input
                            @keyup.enter.native="query"
                            placeholder=""
                            v-model="queryForm.teacherName"
                        />
                    </el-col>
                    <el-col :span="3">
                        <el-input
                            @keyup.enter.native="query"
                            placeholder=""
                            v-model="queryForm.departmentName"
                        />
                    </el-col>
                    <el-col :span="3">
                        <el-button
                            @click="query"
                            type="primary"
                            style="font-size: 18px"
                        >
                            
                        </el-button>
                    </el-col>
                </el-row>
            </div>

            <div class="table">
                <el-table :data="tableData" stripe>
                    <el-table-column label="Id" prop="id" />
                    <el-table-column label="" prop="name" width="200px" />
                    <el-table-column label="" prop="teacherName" />
                    <el-table-column label="" prop="departmentName" />
                    <el-table-column label="" prop="grade" />
                    <el-table-column label="" prop="credit" />
                    <el-table-column
                        align="center"
                        label=""
                        prop="time"
                        width="130px"
                    />
                    <el-table-column label="" prop="selectedCount" />
                    <el-table-column label="" prop="maxSize" />
                    <el-table-column
                        align="center"
                        label=""
                        width="250px"
                        fixed="right"
                    >
                        <template slot-scope="scope">
                            <el-button
                                @click="edit(scope.row.id)"
                                type="success"
                                style="font-size: 18px"
                            >
                                
                            </el-button>
                            <el-button
                                @click="deleteItem(scope.row.id)"
                                type="danger"
                                style="font-size: 18px"
                            >
                                
                            </el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>

            <el-row justify="center" type="flex" style="margin: 5px 0 5px 0">
                <el-pagination
                    :current-page.sync="pageIndex"
                    :page-size="pageSize"
                    :total="pageSize * pageCount"
                    @current-change="getPage"
                    background
                    layout="prev, pager, next"
                >
                </el-pagination>
            </el-row>

            <el-dialog :visible.sync="editing" title="" width="30%">
                <el-form :model="entityForm" label-width="70px" ref="form">
                    <el-form-item label="">
                        <el-input v-model="entityForm.name"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-select
                            placeholder=""
                            v-model="entityForm.teacherId"
                        >
                            <el-option
                                :key="index"
                                :label="item.name"
                                :value="item.id"
                                v-for="(item, index) in teachers"
                            />
                        </el-select>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input
                            type="number"
                            v-model="entityForm.grade"
                        ></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-select v-model="courseDay">
                            <el-option
                                :key="index"
                                :label="item"
                                :value="index"
                                v-for="(item, index) in days"
                            >
                            </el-option>
                        </el-select>
                        <el-select v-model="courseTime">
                            <el-option
                                :key="index"
                                :label="item"
                                :value="index"
                                v-for="(item, index) in times"
                            >
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="()">
                        <el-input
                            type="number"
                            v-model="courseLength"
                        ></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="entityForm.location"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input
                            type="number"
                            v-model="entityForm.credit"
                        ></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input
                            type="number"
                            v-model="entityForm.maxSize"
                        ></el-input>
                    </el-form-item>
                    <!--<el-form-item label="">-->
                    <!--<el-date-picker-->
                    <!--format="yyyy-MM-dd HH:mm"-->
                    <!--type="datetime"-->
                    <!--v-model="entityForm.examDate"-->
                    <!--value-format="yyyy-MM-dd HH:mm"-->
                    <!--&gt;-->
                    <!--</el-date-picker>-->
                    <!--</el-form-item>-->
                    <el-form-item label="">
                        <el-input v-model="entityForm.examLocation"></el-input>
                    </el-form-item>
                </el-form>
                <span class="dialog-footer" slot="footer">
                    <el-button
                        @click="save"
                        style="font-size: 18px"
                        type="primary"
                        >
                         </el-button
                    >
                    <el-button @click="editing = false" style="font-size: 18px"
                        >
                         </el-button
                    >
                </span>
            </el-dialog>
        </div>
    </div>
</template>

<script>
import * as api from "../../api/admin/course";
import * as teacherApi from "../../api/admin/teacher";

export default {
    name: "AdminCourse",
    data() {
        return {
            queryForm: {
                departmentName: "",
                teacherName: "",
                courseName: "",
            },
            entityForm: {},
            tableData: [],
            pageSize: api.pageSize,
            pageCount: 1,
            pageIndex: 1,
            editing: false,
            teachers: [],
            courseDay: "",
            courseTime: "",
            courseLength: 0,
            days: [
                "",
                "",
                "",
                "",
                "",
                "",
                "",
            ],
            times: [
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
            ],
        };
    },
    methods: {
        query() {
            api.getPageCount(
                this.queryForm.departmentName,
                this.queryForm.teacherName,
                this.queryForm.courseName
            ).then((res) => {
                this.pageCount = res;
                this.pageIndex = 1;
                this.getPage(1);
            });
        },
        getPage(pageIndex) {
            api.getPage(
                pageIndex,
                this.queryForm.departmentName,
                this.queryForm.teacherName,
                this.queryForm.courseName
            ).then((res) => {
                this.tableData = res;
            });
        },
        create() {
            this.entityForm = {
                id: -1,
                teacherId: null,
                name: "",
                grade: 2023,
                time: "",
                location: "",
                credit: 2,
                maxSize: 50,
                examDate: null,
                examLocation: null,
            };
            this.courseDay = 1;
            this.courseTime = 1;
            this.courseLength = 2;
            this.editing = true;
        },
        edit(id) {
            api.get(id).then((res) => {
                let split = res.time.split("-");
                this.courseDay = parseInt(split[0]) - 1;
                this.courseTime = parseInt(split[1]) - 1;
                this.courseLength = parseInt(split[2]);
                this.entityForm = res;
                this.editing = true;
            });
        },
        save() {
            let day = this.courseDay + 1;
            let time = this.courseTime + 1;
            this.entityForm.time = day + "-" + time + "-" + this.courseLength;

            if (this.entityForm.id === -1) {
                api.create(this.entityForm).then(() => {
                    this.finishSave();
                });
            } else {
                api.update(this.entityForm).then(() => {
                    this.finishSave();
                });
            }
        },
        finishSave() {
            this.$message.success("");
            this.getPage(this.pageIndex);
            this.editing = false;
        },
        deleteItem(id) {
            api.deleteItem(id).then(() => {
                this.$message.success("");
                this.getPage(this.pageIndex);
            });
        },
        getTeachers() {
            teacherApi.listName().then((res) => {
                this.teachers = res;
            });
        },
    },
    created() {
        this.query();
        this.getTeachers();
    },
};
</script>

