<template>
    <div class="admin-wrap">
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item>
                    <b style="font-size: 22px"> </b>
                </el-breadcrumb-item>
            </el-breadcrumb>
        </div>

        <div class="container">
            <div class="query-form">
                <el-row :gutter="20">
                    <el-col :span="2">
                        <el-button @click="create" style="font-size: 18px"
                            >
                            </el-button
                        >
                    </el-col>
                </el-row>
            </div>

            <div class="table">
                <el-table :data="tableData" stripe>
                    <el-table-column label="Id" prop="id" />
                    <el-table-column label="" prop="username" />
                    <el-table-column label="" prop="permission" />
                    <el-table-column
                        align="center"
                        label=""
                        width="250px"
                        fixed="right"
                    >
                        <template slot-scope="scope">
                            <el-button
                                @click="edit(scope.row.id)"
                                type="success"
                                style="font-size: 18px"
                            >
                                
                            </el-button>
                            <el-button
                                @click="deleteItem(scope.row.id)"
                                type="danger"
                                style="font-size: 18px"
                            >
                                
                            </el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>

            <el-dialog :visible.sync="editing" title="" width="30%">
                <el-form :model="entityForm" label-width="70px" ref="form">
                    <el-form-item label="">
                        <el-input v-model="entityForm.username"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-checkbox-group v-model="priGroup">
                            <el-checkbox label="1" name="priGroup"
                                ></el-checkbox
                            >
                            <el-checkbox label="2" name="priGroup"
                                ></el-checkbox
                            >
                            <el-checkbox label="4" name="priGroup"
                                ></el-checkbox
                            >
                            <el-checkbox label="8" name="priGroup"
                                ></el-checkbox
                            >
                            <el-checkbox label="16" name="priGroup"
                                ></el-checkbox
                            >
                            <el-checkbox label="32" name="priGroup"
                                ></el-checkbox
                            >
                            <el-checkbox label="64" name="priGroup"
                                ></el-checkbox
                            >
                            <el-checkbox label="128" name="priGroup"
                                ></el-checkbox
                            >
                        </el-checkbox-group>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input
                            type="password"
                            v-model="entityForm.password"
                        ></el-input>
                    </el-form-item>
                </el-form>
                <span class="dialog-footer" slot="footer">
                    <el-button
                        @click="save"
                        type="primary"
                        style="font-size: 18px"
                        >
                         </el-button
                    >
                    <el-button @click="editing = false" style="font-size: 18px"
                        >
                         </el-button
                    >
                </span>
            </el-dialog>
        </div>
    </div>
</template>

<script>
import * as api from "../../api/admin/admin";
import Permission from "../../common/permission";

export default {
    name: "AdminAdmin",
    data() {
        return {
            entityForm: {},
            priGroup: [],
            tableData: [],
            editing: false,
        };
    },
    methods: {
        getList() {
            api.list().then((res) => {
                res = this.addPermissionProp(res);
                this.tableData = res;
            });
        },
        addPermissionProp(data) {
            for (let i = 0; i < data.length; i++) {
                let count = 0;
                for (let bit = 0; bit < Permission.permissionCount; bit++) {
                    if (((1 << bit) & data[i].privilege) === 1 << bit) {
                        count++;
                    }
                }
                data[i].permission = count;
            }

            return data;
        },
        create() {
            this.priGroup = [];
            this.entityForm = {
                id: -1,
                username: "",
                privilege: "",
                password: "",
            };

            this.editing = true;
        },
        edit(id) {
            api.get(id).then((res) => {
                this.setPriGroup(res);
                this.entityForm = res;
                this.editing = true;
            });
        },
        setPriGroup(data) {
            let group = [];
            for (let bit = 0; bit < Permission.permissionCount; bit++) {
                if (((1 << bit) & data.privilege) === 1 << bit) {
                    group.push((1 << bit).toString());
                }
            }

            this.priGroup = group;
        },
        readPrivilegeFromGroup() {
            let privilege = 0;
            for (let i = 0; i < this.priGroup.length; i++) {
                privilege += parseInt(this.priGroup[i]);
            }
            return privilege;
        },
        save() {
            this.entityForm.privilege = this.readPrivilegeFromGroup();
            if (this.entityForm.id === -1) {
                api.create(this.entityForm).then(() => {
                    this.finishSave();
                });
            } else {
                api.update(this.entityForm).then(() => {
                    this.finishSave();
                });
            }
        },
        finishSave() {
            this.$message.success("");
            this.getList();
            this.editing = false;
        },
        deleteItem(id) {
            api.deleteItem(id).then(() => {
                this.$message.success("");
                this.getList();
            });
        },
    },
    created() {
        this.getList();
    },
};
</script>

<style scoped></style>
