<template>
    <div class="course-select-wrap">
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item>
                    <b style="font-size: 22px"> </b>
                </el-breadcrumb-item>
            </el-breadcrumb>
        </div>

        <div class="container">
            <div class="query-form">
                <el-row :gutter="20">
                    <el-col :offset="15" :span="3">
                        <el-input
                            @keyup.enter.native="query"
                            placeholder=""
                            v-model="queryForm.courseName"
                        />
                    </el-col>
                    <el-col :span="3">
                        <el-input
                            @keyup.enter.native="query"
                            placeholder=""
                            v-model="queryForm.teacherName"
                        />
                    </el-col>
                    <el-col :span="3">
                        <el-button
                            @click="query"
                            type="primary"
                            style="font-size: 18px"
                        >
                             
                        </el-button>
                    </el-col>
                </el-row>
            </div>

            <div class="table">
                <el-table :data="tableData" stripe>
                    <el-table-column label="Id" prop="courseId" />
                    <el-table-column
                        label=""
                        prop="courseName"
                        width="180px"
                    />
                    <el-table-column label="" prop="teacherName" />
                    <el-table-column label="" prop="credit" />
                    <el-table-column
                        align="center"
                        label=""
                        prop="time"
                        width="130px"
                    />
                    <el-table-column label="" prop="selectedCount" />
                    <el-table-column label="" prop="maxSize" />
                    <el-table-column
                        align="center"
                        label=""
                        width="200px"
                        fixed="right"
                    >
                        <template slot-scope="scope">
                            <el-button
                                @click="select(scope.row.courseId)"
                                :disabled="!allowSelect"
                                type="success"
                                style="font-size: 18px"
                            >
                                 
                            </el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>

            <el-row justify="center" type="flex" style="margin: 5px 0 5px 0">
                <el-pagination
                    :current-page.sync="pageIndex"
                    :page-size="pageSize"
                    :total="pageSize * pageCount"
                    @current-change="getPage"
                    background
                    layout="prev, pager, next"
                >
                </el-pagination>
            </el-row>
        </div>
    </div>
</template>

<script>
import * as api from "../../api/student/courseSelect";
import * as OptionApi from "../../api/option";

export default {
    name: "StudentCourseSelect",
    data() {
        return {
            queryForm: {
                courseName: "",
                teacherName: "",
            },
            tableData: [],
            pageSize: api.pageSize,
            pageCount: 1,
            pageIndex: 1,
            allowSelect: true,
        };
    },
    methods: {
        query() {
            api.getPageCount(
                this.queryForm.courseName,
                this.queryForm.teacherName
            ).then((res) => {
                this.pageCount = res;
                this.pageIndex = 1;
                this.getPage(1);
            });
        },
        getPage(pageIndex) {
            api.getPage(
                pageIndex,
                this.queryForm.courseName,
                this.queryForm.teacherName
            ).then((res) => {
                this.tableData = res;
            });
        },
        select(id) {
            api.select(id).then(() => {
                this.$message.success("!");
                this.getPage(this.pageIndex);
            });
        },
    },
    created() {
        this.query();

    },
    mounted() {
      
        OptionApi.getAllowStudentSelect().then((res) => {
            this.allowSelect = res;
        });
    }
};
</script>

<style scoped></style>
